.first-letter{
  color: rgb(235, 59 ,90);
}

.rem-letters{
  color: rgb(75, 192, 189);
}
nav h1 span{
  margin-right: 0;
}

#hamberger{
  width: 25px;
  height: 25px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  transition: 0.5s ease;
  display: none;


}
#hamberger span{
   width: 100%;
   height: 2px;
   background-color: #f5eeef;
   margin-top: 3px;
   
}

.toggle .item1{
   transform: rotate(45deg) translate(0px, 12px);
}
.toggle .item2{
 opacity: 0;
}
.toggle .item3{
 transform: rotate(-45deg) translate(0px, -12px);
}


.box{
  width : 35%;
   margin: 20px 5%;
   border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
   color: rgb(22, 51, 70);
   transition: .2s all ease-out;
    
}
.box:hover{
  background-color: rgb(22, 51, 70);
  color: white;
}
.menu{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.question{
  color: red;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.question button{
  padding: 5px 8px;
  border: none;
  outline: none;
  color: bisque;
  background: rgb(21, 65, 122);
  border-radius: 5px;
  margin-right: 5px;
}

.sa-sm{
  width:  200px !important;
  height: 300px;
  
}

.icon{
  font-size: 100px;
}

.quiz-name{
  text-align: center;
  font-size: 2rem;
}

a{
  text-decoration: none;
  color: inherit;
}

nav{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(22, 51, 70);
  color: white;
  padding: 0 8px;
}
.nav-sm{
  display: none;
  overflow: hidden;
  justify-content: space-around;
  align-items: center;
 
}


.nav-items{
  display: flex;
  justify-content: space-between;
  width: 35%;
}

@media only screen and (max-width : 650px){
    .menu{
      flex-direction: column;
    }
    .box{
      width: 70%;
    }
    
      #hamberger{
          display: flex;
          transition: 0.5s ease;
      }
      #hamberger span{
          transition: 0.5s ease;
      }
      #hamberger span:hover{
          background-color: rgb(147, 229, 240);
      }

      .nav-items{
        display: none;
      }
      nav{
        justify-content: space-between;
      }
      .nav-sm{
        color: white;
        position: absolute;
        right: -400px;
        height: 200px;
        display: flex;
        flex-direction: column;
        background-color: rgba(0, 0, 0, 0.8);
        z-index: 1;
        transition: 1s ease;
      }

}

